import { Signature } from './signature.model'

/**
 * Signature Mock
 */
export function SignatureMock(options?: Partial<Signature>): Signature {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'SIGNATURE_ID',

    code: options?.code || 'SIGNATURE_CODE',
    walletMessage: options?.walletMessage || 'SIGNATURE_MESSAGE',
    checks: options?.checks || [],

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}
